
"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import axios from "axios"
import Navbar from "@/components/Navbar"
import Footer from "@/components/Footer"
import { User, Building2, Mail, Lock, Loader2 } from "lucide-react"
import styles from "./"

export default function Login() {
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [role, setRole] = useState("USER")
  const [loading, setLoading] = useState(false)
  const router = useRouter()

  const handleLogin = async (e) => {
    e.preventDefault()
    setLoading(true)
    try {
      const endpoint = role === "USER"
        ? `${process.env.NEXT_PUBLIC_BACKEND_BASE_URL}/auth/user/login`
        : `${process.env.NEXT_PUBLIC_BACKEND_BASE_URL}/auth/provider/login`
      const res = await axios.post(endpoint, { email, password })
      localStorage.setItem("token", res.data.token)
      localStorage.setItem("user", JSON.stringify(res.data))
      router.push(role === "USER" ? "/profile/user" : "/profile/provider")
    } catch (err) {
      alert("Login failed: " + (err.response?.data?.message || "Unknown error"))
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className={styles.container}>
      <Navbar />
      <main className={styles.main}>
        <div className={styles.card}>
          <div className={styles.header}>
            <h1 className={styles.title}>Welcome Back</h1>
            <p className={styles.subtitle}>Log in to access your account</p>
          </div>

          <div className={styles.tabs}>
            <div 
              className={`${styles.tab} ${role === "USER" ? styles.tabActive : ''}`}
              onClick={() => setRole("USER")}
            >
              <User size={20} className="mx-auto mb-2" />
              <span>Passenger</span>
            </div>
            <div 
              className={`${styles.tab} ${role === "PROVIDER" ? styles.tabActive : ''}`}
              onClick={() => setRole("PROVIDER")}
            >
              <Building2 size={20} className="mx-auto mb-2" />
              <span>Provider</span>
            </div>
          </div>

          <form onSubmit={handleLogin} className={styles.form}>
            <div className={styles.inputGroup}>
              <Mail className={styles.icon} size={20} />
              <input
                type="email"
                placeholder="Email Address"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className={styles.input}
                required
              />
            </div>

            <div className={styles.inputGroup}>
              <Lock className={styles.icon} size={20} />
              <input
                type="password"
                placeholder="Password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className={styles.input}
                required
              />
            </div>

            <button
              type="submit"
              className={styles.button}
              disabled={loading}
            >
              {loading ? (
                <>
                  <Loader2 className="animate-spin mr-2" size={20} />
                  Logging in...
                </>
              ) : (
                'Login'
              )}
            </button>

            <div className={styles.divider}>
              <span>Or continue with</span>
            </div>

            <div className={styles.socialButtons}>
              <button type="button" className={styles.socialButton}>
                <img src="/google.svg" alt="Google" className="w-5 h-5 mr-2" />
                Google
              </button>
              <button type="button" className={styles.socialButton}>
                <img src="/facebook.svg" alt="Facebook" className="w-5 h-5 mr-2" />
                Facebook
              </button>
            </div>
          </form>
        </div>
      </main>
      <Footer />
    </div>
  )
}
